.section-top-grid{
  width: calc(100% - var(--gutter));
  display: grid;
  grid-template-columns: 1fr 55%;
  grid-column-gap: calc(var(--margin) * 2);
  padding: 0 var(--gutter);
  p {
    font-size: 2vw;
  }
  img{
    transform: translateX(calc(var(--margin) * 2));
  }
  .mobile &{
    padding: 20% 1em 10%;
    display: block;
    width: 100%;
    p{
      font-size: 1em;
    }
    img{
      transform: translateX(0);
    }
  }
}
.section-top-text{
  display: flex;
  flex-direction: column;
  justify-content: center;
  .mobile &{
    margin-bottom: calc(var(--margin) * 3);
  }
}
.section-top-img{
  transform: translateX(var(--margin));
  .mobile &{
    transform: translateX(0);
    margin-top: 1em;
  }
}
.formulario{
  margin-top: calc(var(--gutter)*3);
}
.center{
  text-align: center;
  margin: 0 auto;
}
.form {
  margin-top: var(--gutter);
  --size: 1.1em;
  padding: 0 1em;
  @include from($bp) {
    padding: 0;
  }
  @include from(m) {
    --size: 1.2em;
  }
  @include from(l) {
    --size: 1.3vw;
  }
  font-size: var(--size);
  label, input, select, textarea{
    display: block;
    width: 100%;
    height: calc(var(--size) * 2);
    line-height: calc(var(--size) * 2);
    padding: 0 .5em;
    font-size: var(--size);
    font-family: $font-family;
    border: 1px solid;
  }
  label{
    font-weight: 700;
    font-style: italic;
    border: none;
  }
  input, select, textarea{
    outline: none !important;
    &:focus{
      outline: none;
    }
    &:invalid{
      border-color: #000;
    }
  }
  textarea{
    resize: none;
    height: 5em;
    line-height: 1.2;
    padding: .5em .5em;
  }
  select{
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    background: transparent;
    position: relative;
    z-index: 10;
    padding-right: calc(var(--selectHeigt) + .5em);
  }
  .box.select{
    position: relative;
    height: auto;
  }
  .input-group{
    margin-bottom: 1em;
  }
}
.drowpdawn{
  background: #000;
  width: var(--selectHeigt);
  height: var(--selectHeigt);
  z-index: 1;
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  img{
    width: calc(var(--selectHeigt) * .65);
    height: auto;
  }
}
.box.textarea{
  display: flex;
  flex-direction: column;
}
.textCount{
  align-self: flex-end;
  margin-top: .3em;
  font-size: calc(var(--size) * .6);
}
.form-footer{
  display: flex;
  justify-content: flex-end;
  button{
    border: 1px solid rgba(#000,.8);
    font-size: inherit;
    font-style: italic;
    line-height: 1.8em;
    padding: 0 .8em;
    background: transparent;
    transition: background .3s;
    cursor: pointer;
    &:hover{
      background: darken(#fff,5);
    }
  }
}
